@import '_colors';

body {
  line-height: 1.5 !important;
}

.carousel-bg .carousel-inner .item {
  background-color: darkslategrey;
  background-size: cover;
  background-position: center;
  min-height: 360px;
}

image {
  display: block;
}

.slide {
  margin-top: 30px;
}

.details {
  padding-top: 15px;
  padding-bottom: 30px;
  @extend .helvetica-light;
  font-size: 26px;
}

.details h1 {
  font-size: 26px;
  text-align: center;
  padding-bottom: 40px;
}

.details .back-link {
  text-align: center;
}

.details .back-link a {
  display: inline-block;
  margin: 20px 0;
  padding: 15px 30px;
  background: #333;
  color: #fff;
  border-radius: 24px;
}

.details .back-link a svg {
  margin-right: 10px;
  vertical-align: text-top;
  display: inline-block;
}

.navbar-collapse {
  position: relative;
  padding-top: 30px !important;
  height: 100%;
  border: 0px !important;
}

.navbar-collapse form[role="search"] {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 100%;
  padding: 0px;
  margin: 0px;
  z-index: 2;
  border: 0px !important;
}

.navbar-collapse form[role="search"] button,
.navbar-collapse form[role="search"] input {
  padding: 8px 12px;
  border-radius: 0px;
  border-width: 0px;
  color: rgb(119, 119, 119);
  background-color: rgb(248, 248, 248);
  box-shadow: none;
  outline: none;
}

.input-group {
  width: 100% !important;
}

.navbar-collapse form[role="search"] input {
  padding: 16px 12px;
  font-size: 14pt;
  font-style: italic;
  color: rgb(160, 160, 160);
  box-shadow: none;
}

.navbar-collapse form[role="search"] button[type="reset"] {
  display: none;
}

.navbar-brand {
  position: relative;
  z-index: 1;
}

@media (min-width: 768px) {
  .navbar-collapse {
    padding-top: 0px !important;
    padding-right: 38px !important;
  }
  .navbar-collapse form[role="search"] {
    width: 38px;
  }
  .navbar-collapse form[role="search"] button,
  .navbar-collapse form[role="search"] input {
    padding: 15px 12px;
  }
  .navbar-collapse form[role="search"] input {
    font-size: 18pt;
    opacity: 0;
    display: none;
    height: 50px;
  }
  .navbar-collapse form[role="search"].active {
    width: 100%;
  }
  .navbar-collapse form[role="search"].active button,
  .navbar-collapse form[role="search"].active input {
    display: table-cell;
    opacity: 1;
  }
  .navbar-collapse form[role="search"].active input {
    width: 100%;
  }
}

h2.slogan {
  margin: 0;
  background: yellow;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%)
}

.swiper-container {
  width: 100%;
  height: auto;
  -webkit-transform: translate3d(0, 0, 0);
  z-index: 999999999;
}

body {
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}

.report-thum {
  background: url(../img/report-thumbnail.jpg) no-repeat;
  background-position: 92% 22%;
}

.press-thum {
  background: url(../img/press-thumbnail.jpg) no-repeat;
  background-position: center 60%;
}

.calendar-thum {
  background: url(../img/calendar-thumbnail.jpg) no-repeat;
  background-position: center 69%;
}

.market-thum {
  background: url(../img/market-thumbnail.jpg) no-repeat;
  background-position: center center;
}

.thumbnail {
  width: 100%;
  height: 129px;
  border-radius: 10px !important;
  border-bottom-left-radius: 0px !important;
  border-bottom-right-radius: 0px !important;
  border: 0px !important;
  /* Prevent background color leak outs */
  -webkit-background-clip: padding-box;
  -moz-background-clip: padding;
  background-clip: padding-box;
  margin: 0px !important;
  padding: 0px !important;
}

.list-group.list-group-flush {
  /*padding-left: 4px;
  padding-right: 4px;*/
}

.list-group-item:first-child {
  border-radius: 0px !important;
  border-bottom: 0px !important;
}

li.list-group-item {
  color: $dark-grey;
  border-top: 0px;
  border-radius: 0px;
  font-size: 14px;
  @extend .helvetica-neue;
  span {
    @extend .helvetica-neue-bold;

  }
}

.list-group-item:last-child {
  border-bottom-left-radius: 10px !important;
  border-bottom-right-radius: 10px !important;
}

hr {
  margin: 0.4em 0 !important;
}

.card-block {
