  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  p.market-title {

    @extend .helvetica-neue85-heavy;
    color: $light-blue;
    font-size: 25px;
    line-height: 1.2;

  }
  h4 {
    text-transform: uppercase;
